body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

div.login-from {
  position: absolute;
  top: 3.2em;
  right: 2em;
  height: auto !important;
  padding: 2em !important;
  background-color: white;
  border: 1px solid #dedede;
  z-index: 9999;
}

.create-request {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: flex-end;
  background-color: white;
  padding: 1em;
  border: 1px solid #dedede;
}

.create-request .field {
  margin: 1em 1em 0 1em !important;
  width: 20%;
  min-width: 260px;
}

.create-request .select-request-send-button {
  margin-top: 2em;
  min-width: 260px;
  width: 15%;
}

.map-toggle {
  margin-bottom: 1em !important;
}

.graph-img {
  max-width: 100%;
}

div#root {
  display: flex;
  flex-wrap: wrap;
}

[name="UserWidget"] {
  width: 100%;
  margin: 0;
}

elementree-widget {
  margin: 1.5em;
  flex-grow: 1;
  width: 45%;
}

.create-request-form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  column-gap: 1em;
}

.create-request-map {
  margin: 1em 0;
}

.form.create-request-form .field {
  flex-grow: 1;
  width: 20%;
  margin: 0;
}

.form.create-request-form .Divider {
  width: 100%;
}

.daml-section {
/*   min-height: 48em; */
}

.daml-section .label {
/*   margin-bottom: 1em; */
}

.daml-section .items {
  overflow: auto;
  max-height: 35.4em;
  padding: 0 1em !important;
}

.daml-section .actions {
  margin-top: 1em;
}